}

.page-mobile-menu-active.show .page-main-nav li a {
  color: #ffffff;
}

.page-mobile-menu-active .page-main-nav li a {
  margin-bottom: 10px;
  padding: 5px 0;
}

.page-mobile-menu-active .page-main-nav li a:hover {
  color: #222;
}

.page-main-nav li.active a {
  color: #22eaa0;
}

.page-main-nav li.active a:hover {
  color: #000000;
}

.page-mobile-menu-active .page-main-nav li.active a {
  color: #22eaa0;
}

.page-mobile-menu-active .page-main-nav li {
  display: block;
}

.page-main {
