  z-index: 2;
}

@media screen and (max-width: 750) {
  .page-main {
    margin-bottom: 0;
  }
}

.page-footer {
  position: fixed;
  z-index: 1;
  bottom: 0;
  height: 100px;
  background: #F1F6F8;
  width: 100%;
  left: 0;
  padding-top: 100px;
  text-align: center;
}

@media screen and (max-width: 767.98px) {
  .page-footer {
    position: relative;
    height: inherit;
    padding: 7em 0;
  }
}

.gototop {
  position: fixed;
  bottom: -20px;
  right: 20px;
  z-index: 99999999;
  opacity: 0;
  visibility: hidden;
  transition: .3s all;
}

.gototop.active {
  opacity: 1;
  visibility: visible;
  bottom: 20px;
}

.gototop a {
  width: 50px;
  height: 50px;
  display: table;
  background: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  text-align: center;
  border-radius: 4px;
}

.gototop a i {
  height: 50px;
  display: table-cell;
  vertical-align: middle;
}

.gototop a:hover, .gototop a:active, .gototop a:focus {
  text-decoration: none;
  outline: none;
  background: rgba(0, 0, 0, 0.9);
}

.flex, .page-section-half, .page-section-half .page-text {
  display: flex;
  flex-flow: row wrap;
}

.page-section-half:last-child .page-text {
  border-bottom: none;
}

.page-section-half .page-image, .page-section-half .page-text {
  height: 100vh;
  width: 50%;
  transition: .3s all ease;
}

@media screen and (max-width: 750px) {
  .page-section-half .page-image, .page-section-half .page-text {
    height: inherit;
    padding: 2em 0;
    width: 100%;
  }
}

.page-section-half .page-image {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

@media screen and (max-width: 750px) {
  .page-section-half .page-image {
    height: 400px;
    padding: 0;
  }
}

.page-section-half .page-text {
  border-bottom: 1px solid #f2f2f2;
  justify-content: center;
}

.page-section-half .page-text > .page-text-inner {
  align-self: center;
  max-width: 500px;
  padding: 30px;
}

.page-section-half:hover .page-image, .page-section-half:focus .page-image {
  width: 48%;
}

@media screen and (max-width: 750px) {
  .page-section-half:hover .page-image, .page-section-half:focus .page-image {
    width: 100%;
  }
}

.page-section-half:hover .page-text, .page-section-half:focus .page-text {
  width: 52%;
}

@media screen and (max-width: 750px) {
  .page-section-half:hover .page-text, .page-section-half:focus .page-text {
    width: 100%;
  }
}

.btn {
  border: none;
  border-radius: 4px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;
  text-transform: uppercase;
  letter-spacing: .1em;
  transition: .3s all ease-in;
}

.btn:hover, .btn:focus, .btn:active {
  box-shadow: none;
  text-decoration: none;
}

.btn.btn-sm {
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 13px;
}

.btn.btn-lg {
  padding-top: 20px;
  padding-bottom: 20px;
  line-height: 1.5;
}

.btn.btn-primary {
  border: 2px solid #22eaa0;
  background: #22eaa0;
  color: #ffffff;
}

.btn.btn-primary:hover, .btn.btn-primary:active, .btn.btn-primary:focus {
  background: #39ecaa;
  color: #ffffff;
  border: 2px solid #39ecaa;
}

.btn.btn-primary:focus, .btn.btn-primary:active {
  outline: none;
  border: 2px solid #39ecaa;
}

.page-animated {
  animation-duration: .5s;
}
